import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;
export default {
	data() {
		return {
			navBar: navBar,
			moneyListInfo: '',
			num:''
		};
	},
	methods: {
		leftClick() {
			uni.navigateBack({
				delta: 1
			});
		},
		moneyListInfoa() {
			var that = this;
			var id = that.id;
			var timeStamp = Math.round(new Date().getTime() / 1000);
			var obj = {
				appid: appid,
				id: id,
				timeStamp: timeStamp,
			}
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				id: id,
				timeStamp: timeStamp,
				sign: sign,
			}
			rs.getRequests("moneyListInfo", data, (res) => {
				if (res.data.code == 200) {
					that.moneyListInfo = res.data.data;
					that.num=res.data.data.list.length;
				}
			})
		}
	},
	onLoad(options) {
		this.id = options.id;
	},
	/**
	 * 生命周期函数--监听页面显示
	 */
	onShow() {
		getApp().globalData.isReload=false;
		var that = this;
		that.moneyListInfoa();
	},
};
